import { useNavigate, useLocation } from "react-router-dom";
import { Container, Content, Panel, Sidebar, Footer, RadioGroup, ButtonGroup, List, Pagination, Message, Slider, SelectPicker, Checkbox, Input, Radio, InputGroup, DateRangePicker, DatePicker, useToaster, Drawer, Tag, Avatar, Button, Placeholder, Toggle } from 'rsuite';
import { useState, useEffect } from 'react'
import FormGroup from 'rsuite/esm/FormGroup';
import { socket } from '../socket_context/sockets'
import FlexboxGridItem from "rsuite/esm/FlexboxGrid/FlexboxGridItem";
import Sidenav from "./sidenav";
import SearchIcon from '@rsuite/icons/Search';
import ReactJson from 'react-json-view'

let timer = 0;
export default function VMs() {
    const [vms, setVMs] = useState([])

    const n = useNavigate();
    const l = useLocation();

    const [antiCache, setAntiCache] = useState(performance.now())
    const [updater, setUpdater] = useState()
    const [ sendForm, setSendForm ] = useState(false)

    const [maxPages, setMaxPages] = useState(0)
    const [openCreateTask, setOpenCreateTask] = useState(false);
    const [openAddTask, setOpenAddTask] = useState(false);
    const [openLogsWindow, setOpenLogsWindow] = useState(false);
    const [openCommand, setOpenCommand] = useState(false);
    const [logs, setLogs] = useState()
    const [command, setCommand] = useState()
    const [commandLogs, setCommandLogs] = useState()
  
    useEffect(() => {
        socket.emit("listen_for", {room: "/vms"})
        socket.on("ping", () => {
            getVMs()
            setUpdater()
        })
    }, [])

    useEffect(() => {
        setInterval(() => {
          setAntiCache(performance.now())
        }, 10000)
    }, [])

    const sendTimeout = () => {
        if (timer) {
            clearTimeout(timer)
        }

        timer = setTimeout(() => {
            console.log(1)
            getVMs()
        }, 500)
    }

    const createTask = () => {
        fetch(`http://localhost:5000/api/vm/createVM/${taskForm.name}/${taskForm.os}`, {
        method: "POST",
        body: JSON.stringify(taskForm),
        credentials: 'include',
        headers: {
            "Content-Type": "application/json",
            // 'Content-Type': 'application/x-www-form-urlencoded',
            }
        }).then(data => data.status == 200 ? data.json() : null).then(
            data => {
                if ('status' in data && 'message' in data) {
                    toaster.push(
                      <Message showIcon type={statusToType[data.status]}>
                        {data.message}
                      </Message>,
                      { placement: 'bottomEnd'}
                    )
                  }
            }
        )
    }

    const addVM = (ip, socket) => {
      fetch(`http://localhost:5000/api/vm/addVM/${taskForm.name}/${taskForm.os}`, {
      method: "POST",
      body: JSON.stringify(addForm),
      credentials: 'include',
      headers: {
          "Content-Type": "application/json",
          // 'Content-Type': 'application/x-www-form-urlencoded',
          }
      }).then(data => data.status == 200 ? data.json() : null).then(
          data => {
              if ('status' in data && 'message' in data) {
                  toaster.push(
                    <Message showIcon type={statusToType[data.status]}>
                      {data.message}
                    </Message>,
                    { placement: 'bottomEnd'}
                  )
                }
          }
      )
  }
    

    const statusToType = {
        1: 'success',
        0: 'error',
        2: 'warning',
      }


    const [ taskForm, setTaskForm ] = useState({
        name: "Новая машина",
        os: "ubuntu",
        ip: "127.0.0.1",
        port: 1000,
        vm_type: "qemu",
        ip_qmp: "127.0.0.1",
        path: "ubuntu.iso"
    });

    const [ commandForm, setCommandForm ] = useState({
        cmd: "",
        args: {}
    });

    const [ addForm, setAddForm ] = useState({
      name: "Новая машина",
      os: "ubuntu",
      ip: "127.0.0.1",
      port: 1000,
      vm_type: "qemu",
      ip_qmp: "0.0.0.0",
      path: "/home/vboxuser/ubuntu.iso"
  });
    
    useEffect(() => {
        if (sendForm) {
            setSendForm(false)
            getVMs()
        }
    }, [sendForm])

    useEffect(() => {

    }, [updater])

    const navigator = (data) => {
        socket.emit("unsub_all")
        n(data)
      }
  
    useEffect(() => {
        socket.emit("listen_for", {room: l.pathname})
    }, [])

    const [searchFilters, setFilters] = useState({
        page: 1,
        count: 10,
        query: "",
        os: "all",
        status: "all",
        sort_by: "name",
        order: "asc"
    }); 

    const runVM = (id) => {
      fetch(`http://localhost:5000/api/vm/run/${id}`, {
          method: "POST",
          credentials: 'include',
          headers: {
              "Content-Type": "application/json",
            },
          } ).then(data => data.status == 200 ? data.json() : null).then(
              data => {
                  if (!data.status) { return }
                  setTimeout(() => {
                    if (data.need_hook) {
                        hookVM(id)
                    }
                  }, 2000)

              }
          ).catch(err => console.log(err))
  }

  const hookVM = (id) => {
    fetch(`http://localhost:5000/api/vm/hook/${id}`, {
        method: "POST",
        credentials: 'include',
        headers: {
            "Content-Type": "application/json",
          },
        } ).then(data => data.status == 200 ? data.json() : null).then(
            data => {
                if (!data.status) { return }

                
            }
        ).catch(err => console.log(err))
}

const openLogs = (id) => {
    setLogs()
    fetch(`http://localhost:5000/api/vm/getLogs/${id}`, {
        method: "POST",
        credentials: 'include',
        headers: {
            "Content-Type": "application/json",
          },
        } ).then(data => data.status == 200 ? data.json() : null).then(
            data => {
                if (!data.status) { return }

                setLogs(data.result[0])
            }
        ).catch(err => console.log(err))
        }

const sendCommand = (id) => {
    setCommandLogs()
    fetch(`http://localhost:5000/api/vm/sendCommand/${id}`, {
        method: "POST",
        body: JSON.stringify(commandForm),
        credentials: 'include',
        headers: {
            "Content-Type": "application/json",
            },
        } ).then(data => data.status == 200 ? data.json() : null).then(
            data => {
                if (!data.status) { return }

                setCommandLogs(data.result)
            }
        ).catch(err => console.log(err))
        }

const stopVM = (id) => {
  fetch(`http://localhost:5000/api/vm/stop/${id}`, {
      method: "POST",
      credentials: 'include',
      headers: {
          "Content-Type": "application/json",
        },
      } ).then(data => data.status == 200 ? data.json() : null).then(
          data => {
                if ('status' in data && 'message' in data) {
                    toaster.push(
                    <Message showIcon type={statusToType[data.status]}>
                        {data.message}
                    </Message>,
                    { placement: 'bottomEnd'}
                    )
                }
          }
      ).catch(err => console.log(err))
}

const removeVM = (id) => {
    fetch(`http://localhost:5000/api/vm/deleteVM/${id}`, {
        method: "POST",
        credentials: 'include',
        headers: {
            "Content-Type": "application/json",
          },
        } ).then(data => data.status == 200 ? data.json() : null).then(
            data => {
                  if ('status' in data && 'message' in data) {
                      toaster.push(
                      <Message showIcon type={statusToType[data.status]}>
                          {data.message}
                      </Message>,
                      { placement: 'bottomEnd'}
                      )
                  }
            }
        ).catch(err => console.log(err))
  }

    const getVMs = () => {
        fetch(`http://localhost:5000/api/vm/getAll`, {
            method: "POST",
            body: JSON.stringify({...searchFilters}),
            credentials: 'include',
            headers: {
                "Content-Type": "application/json",
              },
            } ).then(data => data.status == 200 ? data.json() : null).then(
                data => {
                    if (!data.vms) { return }

                    setVMs(data.vms)
                }
            ).catch(err => console.log(err))
    }

    const toaster = useToaster();

    useEffect(() => {
      getVMs()
    }, [])

    return (
    <Container>
        <Sidebar style={{height: "calc(100vh - 4rem)", overflow: "auto", flex: "0 0 20rem"}}>
            <div  style={{padding: "1rem"}}>
                <Button appearance="primary" block onClick={() => {setOpenCreateTask(true)}}>
                    Создать машину
                </Button>
                <Button appearance="ghost" block onClick={() => {setOpenAddTask(true)}}>
                    Добавить машину
                </Button>  
            </div>
            <Panel header={"Фильтры"} collapsible>
                    <div className="tour-search">
                        <InputGroup>
                            <Input value={searchFilters.query} onChange={(e) => {
                                setFilters({...searchFilters, query: e})
                                sendTimeout()
                            }} placeholder={"Поиск"} />
                            <InputGroup.Addon>
                            <SearchIcon />
                            </InputGroup.Addon>
                        </InputGroup>
                    </div>
                    <div className="tour-search" style={{marginTop: "1rem"}}>
                        <Button onClick={getVMs} appearance="primary" block>
                            Поиск
                        </Button>
                    </div>
                    <div className="tour-search">
                        <div style={{display: "flex", justifyContent: "start", paddingBlock: "1rem"}}>
                            <span style={{fontSize: "0.8rem"}}>Количество на странице: {searchFilters.count}</span>
                        </div>
                        <Slider defaultValue={10} max={50} value={searchFilters.count} progress onChange={(e) => {
                                setFilters({...searchFilters, count: e})
                                sendTimeout()
                            }}/>
                    </div>
                    <div className="tour-search">
                        <div style={{display: "flex", justifyContent: "start", paddingBlock: "1rem"}}>
                            <span style={{fontSize: "0.8rem"}}>ОС</span>
                        </div>
                        <SelectPicker searchable={false} block
                        value={searchFilters.os}
                        defaultValue="all"
                        onChange={(e) => {
                            setFilters({...searchFilters, os: e})
                            setSendForm(true)
                        }}
                        data={[{label: "Все", value: "all"}, {label: "Ubuntu", value: "ubuntu"}, {label: "Шиндоус", value: "windows"}]} />
                    </div>
                    <div className="tour-search">
                        <div style={{display: "flex", justifyContent: "start", paddingBlock: "1rem"}}>
                            <span style={{fontSize: "0.8rem"}}>Статус</span>
                        </div>
                        <SelectPicker searchable={false} block
                        value={searchFilters.status}
                        defaultValue="all"
                        onChange={(e) => {
                            setFilters({...searchFilters, status: e})
                            setSendForm(true)
                        }}
                        data={[{label: "Все", value: "all"}, {label: "Работающие", value: 1}, {label: "Остановленные", value: 0}]} />
                    </div>
                    <div className="tour-search">
                        <RadioGroup inline appearance="picker" style={{width: "100%"}} value={searchFilters.sort_by} onChange={(e) => {setFilters({...searchFilters, sort_by: e}); setSendForm(true)}}>
                            <div style={{display: "flex", justifyContent: "start", paddingInline: "0.5rem", alignItems: "center"}}>
                                <span style={{fontSize: "0.8rem"}}>Сортировка</span>
                            </div>
                            <Radio value={"date"}>Дата</Radio>
                            <Radio value={"name"}>Название</Radio>
                        </RadioGroup>
                    </div>
                    <div className="tour-search">
                        <RadioGroup inline appearance="picker" style={{width: "100%"}} value={searchFilters.order} onChange={(e) => {setFilters({...searchFilters, order: e}); setSendForm(true)}}>
                            <div style={{display: "flex", justifyContent: "start", paddingInline: "0.5rem", alignItems: "center"}}>
                                <span style={{fontSize: "0.8rem"}}>Порядок</span>
                            </div>
                            <Radio value={"desc"}>Убыв.</Radio>
                            <Radio value={"asc"}>Возр.</Radio>
                        </RadioGroup>
                    </div>
                </Panel>
            <Drawer open={openCreateTask} onClose={() => setOpenCreateTask(false)} size="sm">
                            <Drawer.Header>
                                <Drawer.Title>Создание машины</Drawer.Title>
                                <Drawer.Actions>
                                    <Button onClick={() => setOpenCreateTask(false)}>Отмена</Button>
                                    <Button onClick={() => {
                                        createTask()
                                        setOpenCreateTask(false)
                                        }} appearance="primary">
                                    Создать
                                    </Button>
                                </Drawer.Actions>
                            </Drawer.Header>
                            <Drawer.Body>
                                <div className="tour-search">
                                    <Input value={taskForm.name} onChange={(e) => {
                                        setTaskForm({...taskForm, name: e})
                                    }} placeholder={"Название машины"} />
                                </div>
                                <div className="tour-search">
                                  <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>Тип ОС</span>
                                  </div>
                                  <SelectPicker searchable={false} style={{width: "100%"}} block
                                  value={taskForm.os}
                                  defaultValue="all"
                                  onChange={(e) => {
                                    setTaskForm({...taskForm, os: e})
                                  }}
                                  data={[{label: "Ubuntu", value: "ubuntu"}, {label: "Шиндоус", value: "windows"}]} />
                              </div>
                              <div className="tour-search">
                                <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>Пусть до .iso / ID докер контейнера</span>
                                  </div>
                                  <Input value={taskForm.path} style={{width: "100%"}} onChange={(e) => {
                                      setTaskForm({...taskForm, path: e})
                                  }} placeholder={"Выше написано"} />
                              </div>
                              <div className="tour-search">
                                <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>IP адрес для SSH</span>
                                  </div>
                                  <Input value={taskForm.ip} style={{width: "100%"}} onChange={(e) => {
                                      setTaskForm({...taskForm, ip: e})
                                  }} placeholder={"IP адрес машины"} />
                              </div>
                              <div className="tour-search">
                                <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>IP адрес для QMP</span>
                                  </div>
                                  <Input value={taskForm.ip_qmp} style={{width: "100%"}} onChange={(e) => {
                                      setTaskForm({...taskForm, ip_qmp: e})
                                  }} placeholder={"IP адрес машины"} />
                              </div>
                              <div className="tour-search">
                                  <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>Порт для QMP</span>
                                  </div>
                                  <Input value={taskForm.port} style={{width: "100%"}} onChange={(e) => {
                                      setTaskForm({...taskForm, port: e})
                                  }} placeholder={"Порт для QMP сервера"} />
                              </div>
                              <div className="tour-search">
                                  <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>Тип ВМ</span>
                                  </div>
                                  <RadioGroup onChange={(e) => { setTaskForm({...taskForm, vm_type: e}) }} value={taskForm.vm_type} name="radioList" inline appearance="picker" defaultValue="qemu">
                                    <span style={{padding: '7px 2px 7px 12px', display: 'inline-block', verticalAlign: 'middle'}}>Status: </span>
                                    <Radio value="qemu">QEMU</Radio>
                                    <Radio value="docker">Докер</Radio>
                                    <Radio value="para">Паравиртуализация</Radio>
                                  </RadioGroup>
                              </div>
                            </Drawer.Body>
                        </Drawer>
                        <Drawer open={openAddTask} onClose={() => setOpenAddTask(false)} size="sm">
                            <Drawer.Header>
                                <Drawer.Title>Добавление машины</Drawer.Title>
                                <Drawer.Actions>
                                    <Button onClick={() => setOpenAddTask(false)}>Отмена</Button>
                                    <Button onClick={() => {
                                        addVM()
                                        setOpenAddTask(false)
                                        }} appearance="primary">
                                    Добавить
                                    </Button>
                                </Drawer.Actions>
                            </Drawer.Header>
                            <Drawer.Body>
                                <div className="tour-search">
                                    <Input value={addForm.name} onChange={(e) => {
                                        setAddForm({...addForm, name: e})
                                    }} placeholder={"Название машины"} />
                                </div>
                                <div className="tour-search">
                                  <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>Тип ОС</span>
                                  </div>
                                  <SelectPicker style={{width: "100%"}} searchable={false} block
                                  value={addForm.os}
                                  defaultValue="all"
                                  onChange={(e) => {
                                    setAddForm({...addForm, os: e})
                                  }}
                                  data={[{label: "Ubuntu", value: "ubuntu"}, {label: "Шиндоус", value: "windows"}]} />
                              </div>
                              <div className="tour-search">
                                <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>Пусть до .iso / Название докер контейнера</span>
                                  </div>
                                  <Input value={addForm.path} style={{width: "100%"}} onChange={(e) => {
                                      setAddForm({...addForm, path: e})
                                  }} placeholder={"Выше написано"} />
                              </div>
                              <div className="tour-search">
                                <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>IP адрес для SSH</span>
                                  </div>
                                  <Input value={addForm.ip} style={{width: "100%"}} onChange={(e) => {
                                      setAddForm({...addForm, ip: e})
                                  }} placeholder={"IP адрес машины"} />
                              </div>
                              <div className="tour-search">
                                <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>IP адрес для QMP</span>
                                  </div>
                                  <Input value={addForm.ip_qmp} style={{width: "100%"}} onChange={(e) => {
                                      setAddForm({...addForm, ip_qmp: e})
                                  }} placeholder={"IP адрес машины"} />
                              </div>
                              <div className="tour-search">
                                  <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>Порт для QMP</span>
                                  </div>
                                  <Input value={addForm.port} style={{width: "100%"}} onChange={(e) => {
                                      setAddForm({...addForm, port: e})
                                  }} placeholder={"Порт для QMP сервера"} />
                              </div>
                              <div className="tour-search">
                                  <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                      <span style={{fontSize: "0.8rem"}}>Тип ВМ</span>
                                  </div>
                                  <RadioGroup onChange={(e) => { setAddForm({...addForm, vm_type: e}) }} value={addForm.vm_type} name="radioList" inline appearance="picker" defaultValue="qemu">
                                    <span style={{padding: '7px 2px 7px 12px', display: 'inline-block', verticalAlign: 'middle'}}>Status: </span>
                                    <Radio value="qemu">QEMU</Radio>
                                    <Radio value="docker">Докер</Radio>
                                    <Radio value="para">Паравиртуализация</Radio>
                                  </RadioGroup>
                              </div>
                              <div style={{display: "flex", justifyContent: "space-between", alignItems: "center", marginTop: "1rem"}}>
                              <span style={{marginRight: "1rem"}}>Подключение к серверной машине</span>
                              <Toggle style={{marginBlock: "1rem"}} checkedChildren="SSH" unCheckedChildren="Локально" checked={addForm.ssh} onChange={(e) => setAddForm({...addForm, ssh: e})}>
                              </Toggle>
                              </div>
                              
                              {
                                addForm.ssh &&
                                <>
                                <div className="tour-search" style={{marginTop: "1rem"}}>
                                    <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBottom: "0.5rem"}}>
                                        <span style={{fontSize: "0.8rem"}}>SSH USER</span>
                                    </div>
                                    <Input value={addForm.ssh_user} onChange={(e) => {
                                        setAddForm({...addForm, ssh_user: e})
                                    }} placeholder={"Username для входа по SSH"} />
                                </div>
                                <div className="tour-search">
                                    <div style={{display: "flex", justifyContent: "start", width: "100%", paddingBlock: "0.5rem"}}>
                                        <span style={{fontSize: "0.8rem"}}>SSH PASSWORD</span>
                                    </div>
                                    <Input value={addForm.ssh_password} onChange={(e) => {
                                        setAddForm({...addForm, ssh_password: e})
                                    }} placeholder={"Password для входа по SSH"} />
                                </div>
                                </>
                              }
                            </Drawer.Body>
                        </Drawer> 
        </Sidebar>
        <Content style={{height: "calc(100vh - 4rem)", overflow: "auto", display: "flex", flexDirection: "column", justifyContent: "space-between"}}>
            <List>
                {vms && vms.map((vm) => (
                    <List.Item key={vm.id} style={{
                      height: "10rem",
                      display: "flex",
                      alignItems: "center",
                      justifyContent: "space-between",
                    }}>
                      <span style={{display: "flex", alignItems: "center"}}>
                        <img  alt="" style={{height: "8rem", width: "14.2rem", backgroundColor: "black"}} src={vm.running ? ("http://localhost:5000/" + vm.last_dump + "?" + antiCache) : "http://localhost:5000/placeholder_offline.jpg"}></img>
                        <div style={{display: "flex", flexDirection: "column", justifyContent: "start", height: "8rem", marginLeft: "1rem"}}>
                            <span style={{display: "flex", alignItems: "center", flexDirection: "column"}}>{vm.name} #{vm.id}</span>
                            <Tag color="blue" style={{width: "fit-content", marginTop: "0.5rem"}}>{vm.vm_type}</Tag>
                        </div>
                      </span>
                      <span style={{display: "flex", height: "8rem", marginRight: "1rem", alignItems: "end", flexDirection: "column", justifyContent: "space-between"}}>
                        <span>
                          <ButtonGroup>
                            {vm.running ?
                              <Button appearance="primary" color="red" onClick={() => stopVM(vm.id)}>
                                Остановить
                              </Button> :
                              <Button appearance="primary" color="green" onClick={() => runVM(vm.id)}>
                                Запустить
                              </Button>
                            }
                            <Button appearance="primary" color="red" onClick={() => removeVM(vm.id)}>
                              Удалить
                            </Button>
                            { vm.vm_type == "qemu" &&
                            (vm.hooked ?
                            <Button appearance="primary" onClick={() => {setOpenCommand(true); setCommandForm({...commandForm, id: vm.id})}}>
                                Выполнить команду
                            </Button> :
                            <Button appearance="primary" onClick={() => hookVM(vm.id)}>
                            Подключиться
                            </Button>
                            )
                            }
                            {
                                vm.vm_type == "docker" &&
                                <Button appearance="primary" onClick={() => {
                                    openLogs(vm.id)
                                    setOpenLogsWindow(true)
                                }}>
                                    Посмотреть логи
                                </Button>
                            }
                          </ButtonGroup>
                        </span>
                        <span>
                          {vm.running ? "Запущено" : "Остановлено"}
                          {vm.hooked && <Tag color="green" style={{width: "fit-content", marginLeft: "0.5rem"}}>Подключено</Tag>}
                        </span>
                      </span>
                    </List.Item>
                ))}
            </List>
            <Drawer open={openLogsWindow} onClose={() => setOpenLogsWindow(false)}>
                <Drawer.Header>
                    <Drawer.Title>Логи</Drawer.Title>
                </Drawer.Header>
                <Drawer.Body>
                            { logs ? 
                                logs.split("\r").map(
                                    (log, i) => <div style={{display: "flex", justifyContent: "start", width: "100%"}} key={i}>
                                        <span style={{paddingInline: "1rem", paddingBlock: "0.5rem", backgroundColor: "#32383f", display: "flex", justifyContent: "center", alignItems: "center"}}>{i}</span>
                                        <span style={{display: "flex", backgroundColor: "black", padding: "0.5rem", paddingInline: "1rem", justifyContent: "start", width: "100%", textAlign: "start"}}>{log.replace(/\\u([0-9]|[a-fA-F])([0-9]|[a-fA-F])([0-9]|[a-fA-F])([0-9]|[a-fA-F])/g, "")}</span>
                                    </div>
                                )
                              : "Загрузка"
                            }
                 </Drawer.Body>
            </Drawer>
            <Drawer open={openCommand} onClose={() => setOpenCommand(false)}>
                <Drawer.Header>
                    <Drawer.Title>Выполнить команду</Drawer.Title>
                </Drawer.Header>
                <Drawer.Body>
                            <Input
                                style={{width: "100%"}}
                                placeholder="Команда"
                                value={commandForm.command}
                                onChange={(e) => setCommandForm({...commandForm, command: e})}
                            />
                            <Button
                                style={{marginTop: "1rem"}}
                                appearance="primary"
                                block
                                onClick={() => {
                                    sendCommand(commandForm.id)
                                    setCommandForm({...commandForm, command: ""})
                                }}
                            >
                                Выполнить
                            </Button>
                            { commandLogs ? 
                                <ReactJson displayDataTypes={false} displayObjectSize={false} theme="brewer" src={commandLogs}  style={{marginTop: "1rem", padding: "1rem"}}/>
                              : ""
                            }
                 </Drawer.Body>
            </Drawer>
            <Footer style={{display: "flex", justifyContent: "center", padding: "1rem"}}>
                    {
                        maxPages && maxPages > 1 &&
                        <Pagination total={maxPages} limit={searchFilters.count} activePage={searchFilters.page} onChangePage={(e) => {
                            setFilters({...searchFilters, page: e})
                            setSendForm(true)
                        }} />
                    }
            </Footer>
        </Content>
    </Container>)
}